// src/App.js
import React from 'react';
import './Landing.css'

import Divider from '@mui/material/Divider';
import KeyboardDoubleArrowUpRoundedIcon from '@mui/icons-material/KeyboardDoubleArrowUpRounded';
import IconButton from '@mui/material/IconButton';


import ResponsiveAppBar from '../../components/AppBar/Bar';
import PlanCard from '../../components/Cards/PricingCard';

function Landing() {

    const planData = [
        {
            title: 'Demo',
            price: '$0',
            description: '1 Assessment y 1 Vacante',
            features: ['3 candidatos o empleados por proceso'],
        },
        {
            title: 'Light',
            price: '$50.000',
            description: '1 Assessment y 1 Vacante',
            features: ['10 candidatos o empleados por proceso'],
        },
        {
            title: 'Pro',
            price: '$200.000',
            description: '2 Assessment y 2 Vacante',
            features: ['20 candidatos o empleados por proceso'],
        },
        {
            title: 'Personalizado',
            price: 'De acuerdo a tus necesidades',
            description: '',
            features: [''],
        },

    ];

    const handleSectionClick = (id) => {
        const mysection = document.getElementById("section-" + id);
        mysection?.scrollIntoView({ behavior: 'smooth' });
    };



    return (
        <div>
            <ResponsiveAppBar></ResponsiveAppBar>
            <div className='container-section-principal' id="section-Inicio">

                <h1 className='o-main-title'>Procesos de talento humano impulsados por inteligencia artificial</h1>



            </div>

            <div className='sections'>
                <div className='center-container-color'>
                    <div>
                        <h3 onClick={() => handleSectionClick("Sel-y-adq")}>Seleccin y adquisicin de talento</h3>
                    </div>

                    <Divider orientation="vertical" variant="middle" flexItem sx={{ bgcolor: "#b8b8b8" }} />
                    <div>
                        <h3 onClick={() => handleSectionClick("Evaluaciones")}>Evaluaciones de desempeo</h3>
                    </div>
                    <Divider orientation="vertical" variant="middle" flexItem sx={{ bgcolor: "#b8b8b8" }} />
                    <div>
                        <h3 onClick={() => handleSectionClick("Gestion")}>Gestin de conocimiento</h3>
                    </div>
                    <Divider orientation="vertical" variant="middle" flexItem sx={{ bgcolor: "#b8b8b8" }} />
                    <div>
                        <h3>Desarrollo y capacitacin</h3>
                    </div>
                </div>
            </div>


            <div className='sections section-1' id="section-Nosotros">

                <div className="col-lg-6 sub-section">
                    <div className="about-image rmb-50">
                        <img className="wow fadeInBottomLeft" data-wow-duration="2s" src={process.env.PUBLIC_URL + "/Assets/images/about-us.jpg"} alt="About Image" />
                        <div className="about-border"></div>
                    </div>
                </div>
                <div className="col-lg-6 sub-section">
                    <div className="about-content">
                        <div className="section-title mb-25 wow fadeInUp" data-wow-duration="2s">
                            <h2>Nos <span>apasiona</span> contribuir a que los <span>procesos de talento humano </span>
                                sean mas <span>poderosos</span> y <span>cautivadores</span> gracias al uso de la <span>inteligencia artificial</span> y la <span>realidad virtual</span></h2>
                        </div>
                        <p className="wow fadeInUp" data-wow-duration="2s">Kaleido Lab, Infodec y Conocimiento Competitivo se unen para experimentar, combinar y crear soluciones que fusionan
                            lo mejor de las capacidades humanas
                            y la tecnologa.
                        </p>


                    </div>
                </div>



            </div>


{/* ********************* PRICING ****************************** */}
            <div className='sections section-1 section-pricing section-detail o-pt-5' id="section-Precios">

                <h2>Oferta de licencias de uso</h2>
                <p>Consulta los asessments disponibles y adquiere el que ms se ajuste a tu necesidad</p>

                <div className="card-container">
                    {planData.map((plan, index) => (
                        <div className="card-item" key={index}>
                            <PlanCard {...plan} />
                        </div>
                    ))}
                </div>
            </div>



            <div className='sections section-2 services-section' id="section-Servicios">


                <div className="row justify-content-center" style={{ textAlign: "center" }}>
                    <div className="col-lg-7 col-md-8 o-col">
                        <div className="section-title text-center mb-80 wow fadeInUp" data-wow-duration="2s">
                            <h2>Nuestros Servicios.</h2>
                            <p>Activamos soluciones tecnolgicas que agregan valor a los procesos de recursos humanos por medio de la generacin de experiencias novedosas que contribuyen a mejorar los indicadores de desempeo de sus procesos a cargo e incentivar la participacin de los clientes internos en las iniciativas de gestin humana.</p>
                        </div>
                    </div>
                </div>



            </div>


            <div className='sections section-1 o-pt-5' id="section-Sel-y-adq">

                <div className="col-lg-6 sub-section">
                    <div className="about-image rmb-50">
                        <img className="wow fadeInBottomLeft" data-wow-duration="2s" src={process.env.PUBLIC_URL + "/Assets/images/portrait-woman.jpg"} alt="Seleccin y adquisicin imagen" style={{ objectPosition: "revert" }} />
                        <div className="about-border"></div>
                    </div>
                </div>
                <div className="col-lg-6 sub-section">
                    <div className="about-content">
                        <div className="section-title mb-25 wow fadeInUp" data-wow-duration="2s">
                            <h2>Seleccin y adquisicin de Talento</h2>
                        </div>
                        <p className="wow fadeInUp" data-wow-duration="2s">Las entrevistas en video para la seleccin de candidatos soportadas por inteligencia artificial son una alternativa innovadora que multiplica las capacidades de los equipos de profesionales de reas de recursos humanos, seleccin y headhunters. Las funcionalidades de inteligencia artificial se harn cargo de:
                        </p>
                        <ul>
                            <li>Configurar la entrevista de acuerdo al cargo y las competencias</li>
                            <li>Definir palabras claves (positivas o negativas) para identificar alertas en las respuestas de los candidatos</li>
                            <li>Determinar la frecuencia y orden de palabras en la estructura de la respuesta ideal</li>
                            <li>Configurar el entrevistador mediante videos o avatares de realidad virtual</li>
                            <li>Definicin de los pesos de porcentaje de calificacin entre el humano y la inteligencia artificial</li>
                            <li>Integracin con inteligencia artificial para contrastar respuestas de conocimientos generales dados por el evaluado con respuestas entregadas por Chat GPT</li>
                            <li>Crear link de la entrevista para enviarlo a mltiples candidatos</li>
                            <li>Transcripcin de la respuesta del formato de video o audio a texto con el fin de elaborar informes de gestin</li>
                            <li>Anlisis de emociones en tiempo real</li>
                            <li>Categorizacin de candidatos segn criterios de calificacin definidos</li>
                        </ul>


                    </div>
                </div>



            </div>

            <div className='sections section-1 section-1-reverse o-pt-5' id="section-Evaluaciones">

                <div className="col-lg-6 sub-section">
                    <div className="about-image rmb-50">
                        <img className="wow fadeInBottomLeft" data-wow-duration="2s" src={process.env.PUBLIC_URL + "/Assets/images/employees-listening.jpg"} alt="Seleccin y adquisicin imagen" style={{ objectPosition: "revert" }} />
                        <div className="about-border"></div>
                    </div>
                </div>
                <div className="col-lg-6 sub-section">
                    <div className="about-content">
                        <div className="section-title mb-25 wow fadeInUp" data-wow-duration="2s">
                            <h2>Evaluacin de desempeo</h2>
                        </div>
                        <p className="wow fadeInUp" data-wow-duration="2s">Las evaluaciones de desempeo en video soportadas por inteligencia artificial permiten que los colaboradores expresen sus logros con toda la naturalidad y emocin de un pitch. Fusionamos lo mejor de las herramientas tradicionales de medicin de desempeo con las nuevas tecnologas.
                        </p>
                        <p className="wow fadeInUp" data-wow-duration="2s">Las nuevas generaciones de trabajadores, tanto jefes como subalternos, son afines a los formatos de video y capturan ms informacin en este tipo de contenido que en cualquier otro. Las funcionalidades de inteligencia artificial se harn cargo de:

                        </p>
                        <ul>
                            <li>Configurar la evaluacin de acuerdo al cargo, competencias y comportamientos</li>
                            <li>Definir palabras claves (positivas o negativas) para identificar alertas en las respuestas de los colaboradores</li>
                            <li>Configurar el interlocutor mediante videos o avatares de realidad virtual</li>
                            <li>Definicin de los pesos de porcentaje de calificacin entre el jefe y la inteligencia artificial</li>
                            <li>Calificacin por jefe, pares y/o subalternos</li>
                            <li>Transcripcin de la respuesta del formato de video o audio a texto con el fin de elaborar informes de gestin</li>
                            <li>Anlisis de emociones en tiempo real</li>
                            <li>Categorizacin de colaboradores segn los niveles de desempeo definidos</li>
                        </ul>


                    </div>
                </div>



            </div>

            <div className='sections section-1 o-pt-5 section-detail' id="section-Gestion">

                <div className="col-lg-6 sub-section">
                    <div className="container-images">
                        <img className="image-sobre-1" src={process.env.PUBLIC_URL + "/Assets/images/person1.jpg"} alt="Imagen 1" />
                        <img className="image-sobre-2" src={process.env.PUBLIC_URL + "/Assets/images/person2.jpg"} alt="Imagen 2" />
                    </div>

                </div>
                <div className="col-lg-6 sub-section">
                    <div className="about-content">
                        <div className="section-title mb-25 wow fadeInUp" data-wow-duration="2s">
                            <h2>Gestin de conocimiento</h2>
                        </div>
                        <p className="wow fadeInUp" data-wow-duration="2s">Los gemelos digitales construidos a partir de Inteligencia artificial permiten recrear la personalidad de un colaborador clave y estructurar sus conocimientos de forma que puedan estar disponibles en su versin digital para sus sucesores. As aseguramos que dicho capital intelectual sea un activo tangible para la organizacin. Las funcionalidades de inteligencia artificial se harn cargo de:
                        </p>
                        <ul>
                            <li>Parametrizar los rasgos de la personalidad</li>
                            <li>Recrear el aspecto fsico de su gemelo humano</li>
                            <li>Almacenar el conocimiento especfico considerado clave</li>
                            <li>Actualizar el conocimiento en la medida que se identifican vacos</li>
                            <li>Entrenar y reentrenar el gemelo digital para validar las respuestas que ofrece</li>
                            <li>Responder preguntas en formato texto y avatar con audio</li>
                            <li>Acceso simultneo al humano digital por mltiples colaboradores que requieren de su consulta</li>
                        </ul>


                    </div>
                </div>



            </div>

            



            {/* *********** Contacto ********************** **/}

            <div className='sections section-1 o-section-callback o-pt-5 o-pb-5' id="section-Contacto">
                <div className="call-back-shap"></div>
                <div className='sub-section'>
                    <div className="section-title wow fadeInUp" data-wow-duration="2s">
                        <h2>Contctanos.</h2>
                        <p>Djanos un mensaje con tus preguntas.</p>
                    </div>
                </div>
                <div className='sub-section' style={{zIndex:1}}>
                    <form id="call-back-form" className="call-back-form" name="call-back-form" action="#" method="post" style={{width:"100%"}}>
                        
                            
                                <div className="form-group o-form-g">
                                    <input type="text" name="full-name" className="form-control" placeholder="Nombre completo" required />
                                </div>
                            
                            
                                <div className="form-group o-form-g">
                                    <input type="email" name="email-address" className="form-control" placeholder="E-mail" required />
                                </div>
                            
                            
                                <div className="form-group o-form-g">
                                    <input type="text" name="phone-number" className="form-control" placeholder="Telfono" />
                                </div>
                            
                            
                                <div className="form-group o-form-g">
                                    <input type="text" name="subject" className="form-control" placeholder="Asunto" required />
                                </div>
                            
                            
                                <div className="form-group o-form-g">
                                    <input type="text" name="short-text" className="form-control" placeholder="Mensaje" />
                                </div>
                            
                        
                        <div className="form-group call-submit text-center">
                            <button className="theme-btn" type="submit" style={{border:"none"}}>Enviar <i className="fas fa-arrow-right"></i></button>
                        </div>
                    </form>
                </div>

            </div>


            {/* ******************* TIENDA ******************** */}
            <div className='sections section-2 services-section tienda-section' id="section-Tienda">
                <div className='o-col' style={{textAlign:"center"}}>
                    <h2>Tienda</h2>
                    <p>Invitamos a los profesionales de talento humano a que publiquen y comercialicen sus assessments.</p>
                    <p>Crea tu assessment, define su precio, publcalo y recibe ingresos por cada empresa o usuario que lo utilice.</p>
                </div>
            </div>


                        {/* ******************** FOOTER ******************** */}
            
            <div className='copyright-section'>
                <div style={{justifyContent: "flex-start"}}><p>Copyright  <span>2023</span> | All Rights Reserved.</p></div>
                <div style={{justifyContent: "center"}}><IconButton sx={{color:"white", background:"#fe6600"}} onClick={()=>handleSectionClick("Inicio")}>
                        <KeyboardDoubleArrowUpRoundedIcon fontSize="medium"></KeyboardDoubleArrowUpRoundedIcon>
                    </IconButton>
                </div>
                <div style={{justifyContent: "flex-end"}}><p>Powered by <span>Kaleido lab</span></p></div>
            </div>

        </div>
    );
}

export default Landing;